.container {
  margin: 0 auto; // 
  //width: 100%; //   ,    
  min-width: 320px; //  
  max-width: 1300px; //    
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: $tablet-width) {  // $tablet-width = 768px;
  .container {
    width: 768px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (min-width: $desktop-width) { //$desktop-width = 1300px;
  .container {
    width: 1300px;
    padding-left: 40px;
    padding-right: 40px;
  }
}

